  setup() {
    onMounted(()=>{
      let frag2 = document.createRange().createContextualFragment('<link rel="stylesheet" href="/assets/gist.css">')
      // let frag2 = document.createRange().createContextualFragment('<link rel="stylesheet" href="https://github.githubassets.com/assets/gist-embed-be17d52734db769251e4a7901baa0d2f.css">')
      let frag = document.createRange().createContextualFragment('<div id=\"gist110342777\" class=\"gist\">\n    <div class=\"gist-file\" translate=\"no\">\n      <div class=\"gist-data\">\n        <div class=\"js-gist-file-update-container js-task-list-container file-box\">\n  <div id=\"file-iching-explaination-md\" class=\"file my-2\">\n      <div id=\"file-iching-explaination-md-readme\" class=\"Box-body readme blob js-code-block-container p-5 p-xl-6 \">\n    <article class=\"markdown-body entry-content container-lg\" itemprop=\"text\"><h2><a id=\"user-content-二進位與六十四卦\" class=\"anchor\" aria-hidden=\"true\" href=\"#二進位與六十四卦\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>二進位與六十四卦<\/h2>\n<h3><a id=\"user-content-想法\" class=\"anchor\" aria-hidden=\"true\" href=\"#想法\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>想法<\/h3>\n<p>普通的想法為了省去String操作會運用到大量的<strong>if-else<\/strong>和<strong>for loop<\/strong>，所以利用<strong>二進位<\/strong>的方式操作(可參考：Bitwise Operation)，能夠讓程式碼更加精簡。<\/p>\n<h3><a id=\"user-content-兩象\" class=\"anchor\" aria-hidden=\"true\" href=\"#兩象\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>兩象<\/h3>\n<p>將<strong>陰<\/strong>和<strong>陽<\/strong>轉換成二進位，如下表：<\/p>\n<table>\n<thead>\n<tr>\n<th align=\"center\"><\/th>\n<th>Binary<\/th>\n<\/tr>\n<\/thead>\n<tbody>\n<tr>\n<td align=\"center\">陰(⚋)<\/td>\n<td>0<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">陽(⚊)<\/td>\n<td>1<\/td>\n<\/tr>\n<\/tbody>\n<\/table>\n<h3><a id=\"user-content-四象\" class=\"anchor\" aria-hidden=\"true\" href=\"#四象\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>四象<\/h3>\n<p>將6、7、8和9轉換成二進位，如下表：<\/p>\n<table>\n<thead>\n<tr>\n<th align=\"center\"><\/th>\n<th>Binary<\/th>\n<\/tr>\n<\/thead>\n<tbody>\n<tr>\n<td align=\"center\">老陰，6<\/td>\n<td>0110<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">少陽，7<\/td>\n<td>0111<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">少陰，8<\/td>\n<td>1000<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">老陰，9<\/td>\n<td>1001<\/td>\n<\/tr>\n<\/tbody>\n<\/table>\n<h4><a id=\"user-content-判斷陰陽\" class=\"anchor\" aria-hidden=\"true\" href=\"#判斷陰陽\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>判斷陰陽<\/h4>\n<p>如果要分出從二進位中，判斷四象是<strong>陽<\/strong>或是<strong>陰<\/strong>，可以判斷二進位第一位的數字：<\/p>\n<ul>\n<li>0：陰<\/li>\n<li>1：陽<\/li>\n<\/ul>\n<h5><a id=\"user-content-陰陽參考表\" class=\"anchor\" aria-hidden=\"true\" href=\"#陰陽參考表\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>陰陽參考表<\/h5>\n<table>\n<thead>\n<tr>\n<th align=\"center\"><\/th>\n<th align=\"center\">Binary<\/th>\n<th align=\"center\">第一位<\/th>\n<\/tr>\n<\/thead>\n<tbody>\n<tr>\n<td align=\"center\">老陰，6<\/td>\n<td align=\"center\">0110<\/td>\n<td align=\"center\">0<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">少陽，7<\/td>\n<td align=\"center\">0111<\/td>\n<td align=\"center\">1<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">少陰，8<\/td>\n<td align=\"center\">1000<\/td>\n<td align=\"center\">0<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">老陰，9<\/td>\n<td align=\"center\">1001<\/td>\n<td align=\"center\">1<\/td>\n<\/tr>\n<\/tbody>\n<\/table>\n<h5><a id=\"user-content-實作\" class=\"anchor\" aria-hidden=\"true\" href=\"#實作\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>實作<\/h5>\n<div class=\"highlight highlight-source-cs\"><pre><span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">&amp;<\/span> <span class=\"pl-c1\">1<\/span><\/pre><\/div>\n<h4><a id=\"user-content-判斷老少\" class=\"anchor\" aria-hidden=\"true\" href=\"#判斷老少\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>判斷老少<\/h4>\n<p>如果要分出從二進位中，判斷四象是<strong>老<\/strong>或是<strong>少<\/strong>，可以判斷二進位用(第一位數字<strong>XOR<\/strong>第四位數字):<\/p>\n<ul>\n<li>0：老<\/li>\n<li>1：少<\/li>\n<\/ul>\n<h5><a id=\"user-content-老少參考表\" class=\"anchor\" aria-hidden=\"true\" href=\"#老少參考表\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>老少參考表<\/h5>\n<table>\n<thead>\n<tr>\n<th align=\"center\"><\/th>\n<th align=\"center\">Binary<\/th>\n<th align=\"center\">第一位<\/th>\n<th align=\"center\">第四位<\/th>\n<th align=\"center\">XOR<\/th>\n<\/tr>\n<\/thead>\n<tbody>\n<tr>\n<td align=\"center\">老陰，6<\/td>\n<td align=\"center\">0110<\/td>\n<td align=\"center\">0<\/td>\n<td align=\"center\">0<\/td>\n<td align=\"center\">0<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">少陽，7<\/td>\n<td align=\"center\">0111<\/td>\n<td align=\"center\">1<\/td>\n<td align=\"center\">0<\/td>\n<td align=\"center\">1<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">少陰，8<\/td>\n<td align=\"center\">1000<\/td>\n<td align=\"center\">0<\/td>\n<td align=\"center\">1<\/td>\n<td align=\"center\">1<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">老陰，9<\/td>\n<td align=\"center\">1001<\/td>\n<td align=\"center\">1<\/td>\n<td align=\"center\">1<\/td>\n<td align=\"center\">0<\/td>\n<\/tr>\n<\/tbody>\n<\/table>\n<h5><a id=\"user-content-實作-1\" class=\"anchor\" aria-hidden=\"true\" href=\"#實作-1\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>實作<\/h5>\n<p>第一位數字的取法<\/p>\n<div class=\"highlight highlight-source-cs\"><pre><span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">&gt;&gt;<\/span> <span class=\"pl-c1\">3<\/span><\/pre><\/div>\n<p>最後一位的取法<\/p>\n<div class=\"highlight highlight-source-cs\"><pre><span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">&amp;<\/span> <span class=\"pl-c1\">1<\/span><\/pre><\/div>\n<p>結合上述兩個用法，最後可得知老少<\/p>\n<div class=\"highlight highlight-source-cs\"><pre>(<span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">&gt;&gt;<\/span> <span class=\"pl-c1\">3<\/span>) <span class=\"pl-k\">^<\/span> (<span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">&amp;<\/span> <span class=\"pl-c1\">1<\/span>)<\/pre><\/div>\n<h3><a id=\"user-content-八卦\" class=\"anchor\" aria-hidden=\"true\" href=\"#八卦\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>八卦<\/h3>\n<p>首先，要先定義八卦的二進位碼，如下表：<\/p>\n<table>\n<thead>\n<tr>\n<th align=\"center\"><\/th>\n<th align=\"center\">Binary(下到上，右到左)<\/th>\n<th align=\"center\">卦和<\/th>\n<\/tr>\n<\/thead>\n<tbody>\n<tr>\n<td align=\"center\">乾(☰)<\/td>\n<td align=\"center\">111<\/td>\n<td align=\"center\">7<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">兌(☱)<\/td>\n<td align=\"center\">011<\/td>\n<td align=\"center\">3<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">離(☲)<\/td>\n<td align=\"center\">101<\/td>\n<td align=\"center\">5<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">震(☳)<\/td>\n<td align=\"center\">001<\/td>\n<td align=\"center\">1<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">巽(☴)<\/td>\n<td align=\"center\">110<\/td>\n<td align=\"center\">6<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">坎(☵)<\/td>\n<td align=\"center\">010<\/td>\n<td align=\"center\">2<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">艮(☶)<\/td>\n<td align=\"center\">100<\/td>\n<td align=\"center\">4<\/td>\n<\/tr>\n<tr>\n<td align=\"center\">坤(☷)<\/td>\n<td align=\"center\">000<\/td>\n<td align=\"center\">0<\/td>\n<\/tr>\n<\/tbody>\n<\/table>\n<h4><a id=\"user-content-製造八卦\" class=\"anchor\" aria-hidden=\"true\" href=\"#製造八卦\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>製造八卦<\/h4>\n<p>假如得到了三個數字，分別為：6、9、7，一定要把每個數字拆成兩項：<\/p>\n<ul>\n<li>\n<p>陰陽<\/p>\n<blockquote>\n<p>組成可以知道<strong>單卦<\/strong><\/p>\n<\/blockquote>\n<\/li>\n<li>\n<p>老少<\/p>\n<blockquote>\n<p>組成可以知道<strong>變爻<\/strong><\/p>\n<\/blockquote>\n<\/li>\n<\/ul>\n<h5><a id=\"user-content-單卦\" class=\"anchor\" aria-hidden=\"true\" href=\"#單卦\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>單卦<\/h5>\n<p>一開始，N=0，之後由<strong>右向左<\/strong>，一位一位對N操作<\/p>\n<p>Step 1<\/p>\n<div class=\"highlight highlight-source-cs\"><pre><span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">=<\/span> <span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">|<\/span> (<span class=\"pl-c1\">6<\/span> <span class=\"pl-k\">&amp;<\/span> <span class=\"pl-c1\">1<\/span>) <span class=\"pl-k\">&lt;&lt;<\/span> <span class=\"pl-c1\">0<\/span>;\n<span class=\"pl-c\"><span class=\"pl-c\">//<\/span> (6 &amp; 1) =&gt; 取第一位，陽為1，陰爲0<\/span>\n<span class=\"pl-c\"><span class=\"pl-c\">//<\/span> X &lt;&lt; 0  =&gt; 向左移0位<\/span>\n<span class=\"pl-c\"><span class=\"pl-c\">//<\/span> N | X 加入N裡<\/span><\/pre><\/div>\n<p>Step 2<\/p>\n<div class=\"highlight highlight-source-cs\"><pre><span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">=<\/span> <span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">|<\/span> (<span class=\"pl-c1\">9<\/span> <span class=\"pl-k\">&amp;<\/span> <span class=\"pl-c1\">1<\/span>) <span class=\"pl-k\">&lt;&lt;<\/span> <span class=\"pl-c1\">1<\/span>;<\/pre><\/div>\n<p>Step 3<\/p>\n<div class=\"highlight highlight-source-cs\"><pre><span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">=<\/span> <span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">|<\/span> (<span class=\"pl-c1\">7<\/span> <span class=\"pl-k\">&amp;<\/span> <span class=\"pl-c1\">1<\/span>) <span class=\"pl-k\">&lt;&lt;<\/span> <span class=\"pl-c1\">2<\/span>;<\/pre><\/div>\n<h5><a id=\"user-content-變爻\" class=\"anchor\" aria-hidden=\"true\" href=\"#變爻\"><svg class=\"octicon octicon-link\" viewBox=\"0 0 16 16\" version=\"1.1\" width=\"16\" height=\"16\" aria-hidden=\"true\"><path fill-rule=\"evenodd\" d=\"M7.775 3.275a.75.75 0 001.06 1.06l1.25-1.25a2 2 0 112.83 2.83l-2.5 2.5a2 2 0 01-2.83 0 .75.75 0 00-1.06 1.06 3.5 3.5 0 004.95 0l2.5-2.5a3.5 3.5 0 00-4.95-4.95l-1.25 1.25zm-4.69 9.64a2 2 0 010-2.83l2.5-2.5a2 2 0 012.83 0 .75.75 0 001.06-1.06 3.5 3.5 0 00-4.95 0l-2.5 2.5a3.5 3.5 0 004.95 4.95l1.25-1.25a.75.75 0 00-1.06-1.06l-1.25 1.25a2 2 0 01-2.83 0z\"><\/path><\/svg><\/a>變爻<\/h5>\n<p>一開始，N=0，之後由<strong>右向左<\/strong>，一位一位對N操作<\/p>\n<p>Step 1<\/p>\n<div class=\"highlight highlight-source-cs\"><pre><span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">=<\/span> <span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">|<\/span> ( (<span class=\"pl-c1\">6<\/span> <span class=\"pl-k\">&amp;<\/span> <span class=\"pl-c1\">1<\/span>)<span class=\"pl-en\">NXOR<\/span>(<span class=\"pl-c1\">6<\/span> <span class=\"pl-k\">&gt;&gt;<\/span> <span class=\"pl-c1\">3<\/span>) ) <span class=\"pl-k\">&lt;&lt;<\/span> <span class=\"pl-c1\">0<\/span>;\n<span class=\"pl-c\"><span class=\"pl-c\">//<\/span> (6 &amp; 1)NXOR(6 &gt;&gt; 3) =&gt; 得到老少，因為用了\`NXOR\`，所以老為1, 少為0<\/span><\/pre><\/div>\n<p>Step 2<\/p>\n<div class=\"highlight highlight-source-cs\"><pre><span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">=<\/span> <span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">|<\/span> ( (<span class=\"pl-c1\">7<\/span> <span class=\"pl-k\">&amp;<\/span> <span class=\"pl-c1\">1<\/span>)<span class=\"pl-en\">NXOR<\/span>(<span class=\"pl-c1\">7<\/span> <span class=\"pl-k\">&gt;&gt;<\/span> <span class=\"pl-c1\">3<\/span>) ) <span class=\"pl-k\">&lt;&lt;<\/span> <span class=\"pl-c1\">1<\/span><\/pre><\/div>\n<p>Step 3<\/p>\n<div class=\"highlight highlight-source-cs\"><pre><span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">=<\/span> <span class=\"pl-smi\">N<\/span> <span class=\"pl-k\">|<\/span> ( (<span class=\"pl-c1\">9<\/span> <span class=\"pl-k\">&amp;<\/span> <span class=\"pl-c1\">1<\/span>)<span class=\"pl-en\">NXOR<\/span>(<span class=\"pl-c1\">9<\/span> <span class=\"pl-k\">&gt;&gt;<\/span> <span class=\"pl-c1\">3<\/span>) ) <span class=\"pl-k\">&lt;&lt;<\/span> <span class=\"pl-c1\">2<\/span><\/pre><\/div>\n<\/article>\n  <\/div>\n\n  <\/div>\n<\/div>\n\n      <\/div>\n      <div class=\"gist-meta\">\n        <a href=\"https://gist.github.com/littlemoonstones/d24d1f958c387124d15534e66136c904/raw/844c46f09ab87015b37e52dc363e8c319b223656/IChing-explaination.md\" style=\"float:right\">view raw<\/a>\n        <a href=\"https://gist.github.com/littlemoonstones/d24d1f958c387124d15534e66136c904#file-iching-explaination-md\">IChing-explaination.md<\/a>\n        hosted with &#10084; by <a href=\"https://github.com\">GitHub<\/a>\n      <\/div>\n    <\/div>\n<\/div>\n')
